"use client";
import { motion } from "framer-motion";
import { FaHome, FaLaptopCode, FaServer, FaEnvelope } from "react-icons/fa";
import { useState } from "react";

const items = [
  { id: "inicio", icon: <FaHome />, label: "Inicio" },
  { id: "tecnologias", icon: <FaLaptopCode />, label: "Tecnologías" },
  { id: "proyectos", icon: <FaServer />, label: "Proyectos" },
  { id: "contacto", icon: <FaEnvelope />, label: "Contacto" },
];

export default function BottomDockNav() {
  const [active, setActive] = useState("inicio");

  const scrollTo = (id: string) => {
    const el = document.getElementById(id);
    if (!el) return;

    const elTop = el.offsetTop;
    const navHeight = 0; // Si tienes un header fijo, puedes sumar su alto aquí
    const target = elTop - navHeight;

    window.scrollTo({
      top: target,
      behavior: "smooth",
    });

    setActive(id);
  };

  return (
    <motion.div
      initial={{ y: 100, opacity: 0 }}
      animate={{ y: 0, opacity: 1 }}
      transition={{ duration: 0.6 }}
      className="fixed bottom-6 left-1/2 -translate-x-1/2 z-50 flex gap-6
                 bg-white/30 dark:bg-gray-800/50 backdrop-blur-lg 
                 border border-white/10 rounded-full px-8 py-3 shadow-xl"
    >
      {items.map((it) => {
        const isActive = active === it.id;
        return (
          <motion.button
            key={it.id}
            onClick={() => scrollTo(it.id)}
            whileHover={{ scale: 1.2 }}
            whileTap={{ scale: 0.9 }}
            className={`flex flex-col items-center text-xs transition-all ${
              isActive ? "text-cyan-400" : "text-gray-400 hover:text-cyan-300"
            }`}
          >
            <motion.div
              animate={{ scale: isActive ? 1.4 : 1 }}
              transition={{ type: "spring", stiffness: 200, damping: 10 }}
            >
              {it.icon}
            </motion.div>
            <span className="hidden sm:block mt-1">{it.label}</span>
          </motion.button>
        );
      })}
    </motion.div>
  );
}
